








import Vue from "vue";
import Layout from "@/layout/harvest-layout.json";
import { calculateCellPosition, calculateAreaPixelSize } from "@/utils/cell-calc";

export default Vue.extend({
  name: "GridDisplay",
  props: {
    size: { type: [Number], default: 20 },
    row: { type: [Number], default: 42 },
    column: { type: [Number], default: 42 },
    greyscale: { type: Boolean, default: false }
  },
  data() {
    return {
      cellData: Layout,
      color: [null, "green", "pink", "blue", "brown"],
      greyscaleColor: [null, "#303030", "#999999", "#E5E5E5", "#E5E5E5"]
    };
  },
  computed: {
    elementStyle() {
      const size = calculateAreaPixelSize(
        [
          [1, 1],
          [this.row, this.column]
        ],
        this.size
      );
      return {
        height: `${size[0]}px`,
        width: `${size[1]}px`
      };
    }
  },
  methods: {
    cellStyle(i: number, j: number) {
      const [xPos, yPos] = calculateCellPosition([i, j], this.size);
      return {
        height: `${this.size}px`,
        width: `${this.size}px`,
        top: `${xPos}px`,
        left: `${yPos}px`,
        "background-color": (this.greyscale ? this.greyscaleColor : this.color)[this.cellData[i - 1]?.[j - 1]]
      };
    }
  }
});
